/*Navigation Bar Styling*/

    .nav-wrapper{
        width: 100%;
        overflow: hidden;
        pointer-events:none;
        margin: 0;
        position: fixed;
        height: 200px;
        /* border-bottom-color: white; */
        z-index: 9000;
        transition: all .5s ease-out;
        @media screen and (max-width: $break-small) {
        }
    }

    .nav-row{
        padding-top: 10px;
        padding-bottom: 10px;
        padding-left: 10px;
        padding-right: 10px;
        border-bottom-width: 1px;
        border-bottom-style: solid;
        background-color: rgba(0,0,0,0);
    }

    .nav-shadow{
        width: 100%;
        height: 98px;
        position: fixed;
        z-index: -1;
    }

    .nav-wrapper .row{
        margin-bottom: 15px;
        pointer-events:auto;
        @media screen and (max-width: $break-small) {
            margin-bottom: 5px;
        }
    }

    .nav-menu-default{
        padding-right: 20px;
    }

    .nav-main-menu{
        text-align: left;
    }

    .nav-first-letter{
        font-size: 24pt;
        @media screen and (max-width: $break-large) {
            font-size: 20pt;
        }
        @media screen and (max-width: $break-medium) {
            font-size: 16pt;
        }
    }

    .nav-header-text{
        font-size: 20pt;
        font-weight: 500;
        color: white;
        padding: 4px 10px 5px 10px;
        margin-right: 20px;
        text-decoration: none;
        letter-spacing: 1px;
        word-spacing: 5px;
        font-family: 'Montserrat', sans-serif;
        position: relative;
        @media screen and (max-width: $break-large) {
            padding: 8px 10px 5px 10px;
            font-size: 16pt;
        }
        @media screen and (max-width: $break-medium) {
            font-size: 12pt;
            padding: 10px 10px 5px 10px;
            margin-right: 0px;
        }
    }

    .nav-header-text:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        bottom: 0;
        left: 0;
        background-color: #FFF;
        visibility: hidden;
        transform: scaleX(0);
        transition: all 0.3s ease-in-out;
    }

    .nav-header-text:hover:before{
        visibility: visible;
        transform: scaleX(.9);
    }

    .nav-header-link{
        text-decoration:none;
        cursor: pointer;
        letter-spacing: 1px;
    }

    .nav-home-button{
        border-radius: 15px;
        padding-bottom: 5px;
        height: 100%;
        text-transform: none;
        display: inline-block;
        vertical-align: top;
        border-color: darkslategray;
        background-color: teal;
        border-style: solid;
        border-width: 2px;
    }

    .nav-logo{
        display:inline-block;
        padding-right: 5%;
        @media screen and (max-width: $break-small) {
            display: none;
        }
    }

    .nav-logo-mobile{
        display: none;
        @media screen and (max-width: $break-small) {
            display: inline-block;
        }
    }

    .nav-item{
        vertical-align: top;
        text-align: center;
        font-weight: bold;
        font-size: 14pt;
        padding-left: 10%;
        padding-top:12px;
        display: inline-block;
        transition: padding-right .2s;
        // &:hover{
        //     padding-right: 3%;
        //     transition: padding-right .2s;
        //     @media screen and (max-width: $break-small) {
        //         padding-right: 5%;
        //     }
        // }
        @media screen and (max-width: $break-small) {
            display: none;
        }
    }

    .nav-item-text{
        color:white;
        font-weight: 300;
        position: relative;
        margin-left: 15px;
        margin-right: 0px;
        top:-10px;
        display: inline-block;
        transition: all .2s;
        opacity: 0;
        max-width: 0px;
        white-space: nowrap;
        @media screen and (max-width: $break-small) {
            opacity: 1;
        }
    }

    .nav-extender{
        height: 200px;
        width: 100%;
        position: fixed;
    }

    .nav-item-tooltip{
        pointer-events: none;
        visibility: hidden;
        width: 100px;
        background-color: black;
        text-align: center;
        padding: 5px 0;
        border-radius: 6px;
        right: -100%;
        top: 70px;
        
        /* Position the tooltip text - see examples below! */
        position: absolute;
        z-index: 999;
        color:white;
        font-weight: 300;
        transition: all .2s;
        opacity: 1;
        // &:after{
        //     content: " ";
        //     position: absolute;
        //     bottom: 100%;  /* At the top of the tooltip */
        //     left: 50%;
        //     margin-left: -5px;
        //     border-width: 5px;
        //     border-style: solid;
        //     border-color: transparent transparent black transparent;
        // }
        @media screen and (max-width: $break-small) {
            opacity: 1;
        }
    }

    .nav-item-tooltip::after{
        content: " ";
        position: absolute;
        bottom: 100%;  /* At the top of the tooltip */
        left: 50%;
        margin-left: -5px;
        border-width: 6px;
        border-style: solid;
        border-color: transparent transparent black transparent;
    }

    .nav-item-link{
        cursor: pointer;
        color:white;
        font-weight: 100;
        position: relative;
        display: inline-block;
        &:hover{
            .nav-item-tooltip{
                visibility: visible;
            }
        }
        // &:hover{
        //     .nav-item-text{
        //         opacity: 1;
        //         transition: all .8s;
        //         max-width: 200px;
        //     }
        // }
        @media screen and (max-width: $break-small) {
            margin-left: -45px;
        }
    }

    .nav-item-link:before{
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        bottom: 0;
        left: 0;
        background-color: #FFF;
        visibility: hidden;
        transform: scaleX(0);
        transform-origin: left;
        transition: all 0.3s ease-in-out;
    }

    .nav-item-link:hover:before{
        visibility: visible;
        transform: scaleX(1);
        @media screen and (max-width: $break-small) {
            visibility: hidden;
        }
    }

    #nav-image{
        height: 35px;
        display: inline-block;
        @media screen and (max-width: $break-medium) {
            margin-top: 5px;
            height: 30px;
        }
    }

// Nav Mobile Menu

    .nav-mobile-menu{
        display: none;
        text-align: right;
        @media screen and (max-width: $break-small) {
            display: inline-block;
        }
    }

    .nav-menu{
