        border-bottom: solid gray 2px;
    }

    .nav-item-mobile{
        border-top: solid gray 2px;
        padding: 20px 0px;
        text-align: center;
    }

    .nav-item-spacer{
        height: 62px;
    }

/* Home Page Styling */

