        height: 200px;
        position: relative;
        opacity: 1;
        transition: all 1s;
        -webkit-filter: drop-shadow(0px 0px 0px rgba(0, 225, 255, 0));
        filter: drop-shadow(0px 0px 0px rgba(0, 225, 255, 0));
        &:hover{
            transition: all 1s;
            transform: scale(1.1, 1.1);
            opacity: 1;
            -webkit-filter: drop-shadow(0px 0px 50px rgba(0, 225, 255, 7));
            filter: drop-shadow(0px 0px 50px rgba(0, 225, 255, .7));
        }
        @media screen and (max-width: $break-small) {
            display: none;
        }
    }

    #logo-mobile{
        cursor: pointer;
        width: 33vw;
        height: auto;
        position: relative;
        text-align: center;
        display: none;
        opacity: 1;
        transition: all 1s;
        -webkit-filter: drop-shadow(0px 0px 0px rgba(0, 225, 255, 0));
        filter: drop-shadow(0px 0px 0px rgba(0, 225, 255, 0));
        &:hover{
            transition: all 1s;
            transform: scale(1.1, 1.1);
            opacity: 1;
            -webkit-filter: drop-shadow(0px 0px 50px rgba(0, 225, 255, 7));
            filter: drop-shadow(0px 0px 50px rgba(0, 225, 255, .7));
        }
        @media screen and (max-width: $break-small) {
