// About Me Styling

    .about-me-banner{
        // height: calc(100vh - 71px);
        max-height: 600px;
        width: 100vw;
        /* background: linear-gradient(180deg, rgba(0, 0, 0, 0.582) 0%, rgb(255, 255, 255) 2%, rgb(255, 255, 255) 100%); */
