            text-align: center;
            padding-bottom: 50px;
        }
    }

    .about-row{
        height: inherit;
        display: flex;
        flex-wrap:wrap;
        margin-bottom:0px;
    }
    .about-col-left{
        height: inherit;
        text-align: right;
        vertical-align: middle;
        line-height: 600px;
        width: 25vw;
        @media screen and (max-width: $break-medium) {
            height: auto;
            vertical-align: auto;
            line-height: normal;
            width: 100vw;
            text-align: center;
            padding: 50px 0px;
        }
    }
    .about-col-right{
        height: inherit;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap:wrap;
        width: 75vw;
        @media screen and (max-width: $break-medium) {
            width: 100vw;
        }
