    }

    .avatar{
        height: 300px;
        border-radius:50%;
        box-shadow: rgba(0,0,0,0.8) -5px 10px 20px;
        vertical-align: middle;
    }

    .about-me-title{
        display: block;
        min-width: 100%;
    }

